import "../../styles/App.css";
// import "antd/dist/antd.css";
import logo from "../../../src/logo.svg";

import {
    AppstoreOutlined,
    FileDoneOutlined,
    OrderedListOutlined,
    PieChartOutlined,
    ReloadOutlined,
    SettingOutlined,
    TeamOutlined,
} from "@ant-design/icons";
import { Layout, Menu } from "antd";
import React, { useState, useEffect } from "react";
import { Link, useNavigate } from "react-router-dom";
import { LogOut, User } from "react-feather";
import StoreProvider from "../../store/Provider";
const { Header, Content, Sider } = Layout;
const roles = localStorage.getItem("roles");

function getItem(label, key, icon, children) {
    return {
        key: key,
        label: label,
        icon: icon,
        children: children,
    };
}
const isLogin = localStorage.getItem("token");
const items = [
    getItem("Trang ch", "/admin/statistical", <PieChartOutlined />),
    getItem("Bn hng", "/admin/order/create", <OrderedListOutlined />),
    getItem("Xem n Hng", "sub2", <OrderedListOutlined />, [
        getItem("t hng", "/admin/order/create"),
        getItem("Xc nhn thanh ton", "/admin/order/confirm/payment"),
        getItem("Qun l n t hng", "/admin/order"),
        getItem("Xc nhn n hng", "/admin/order/confirm"),
        getItem("n hng ch ly hng", "/admin/order/wait"),
        getItem("n hng ang giao", "/admin/order/delivering"),
        getItem("n hng  nhn", "/admin/order/success"),
        getItem("n hng  hu", "/admin/order/cancel"),
    ]),
    getItem("Qun l dch v", "/admin/discount", <FileDoneOutlined />),
    getItem("Qun l sn phm", "sub1", <AppstoreOutlined />, [
        getItem("Danh sch", "/admin/product"),
        getItem("Loi c ph", "/admin/typecoffee"),
        getItem("Thm mi", "/admin/product/create"),
        getItem("Qun l linh kin", "/admin/accessories"),
        getItem("Sn phm tn kho", "/admin/product/inventory"),
        getItem("Sn phm gim gi", "/admin/product/discountproduct"),
    ]),
    getItem("Qun l h thng", "sub3", <TeamOutlined />, [
        getItem("Qun l ngi dng", "/admin/user"),
        getItem("Qun l nhn vin", "/admin/staff"),
        getItem("Qun l vai tr", "/admin/setting/role"),
    ]),
    getItem("Yu cu h tr", "sub4", <ReloadOutlined />, [
        getItem("Yu cu i hng", "/admin/order/exchange"),
    ]),
    getItem("Thng tin c nhn", "/admin/user/profile", <SettingOutlined />),
    getItem("ng nhp", "/login", <SettingOutlined />),
];

const DefaultLayout = ({ children }) => {
    const [collapsed, setCollapsed] = useState(false);
    const [userName, setUserName] = useState();
    const navigate = useNavigate();

    useEffect(() => {
        if (localStorage.getItem("username") != undefined) {
            setUserName(localStorage.getItem("username"));
        }
    }, []);

    const login = () => {
        navigate("/login");
    };

    const logout = () => {
        window.location.href = "/login";
        localStorage.removeItem("roles");
        localStorage.removeItem("username");
        localStorage.removeItem("information");
        localStorage.removeItem("id");
        localStorage.removeItem("token");
        localStorage.removeItem("carts");
    };
    return (
        <StoreProvider>
            <Layout>
                <Sider
                    style={{
                        boxShadow: "2px 0 6px #00152959",
                        overflow: "auto",
                        height: "94vh",
                        left: 0,
                        top: 0,
                        bottom: 0,
                    }}
                    collapsible
                    collapsed={collapsed}
                    onCollapse={(value) => setCollapsed(value)}
                >
                    <div className="logo">
                        <Link to={"/user"}>
                            <img style={{ width: "100%" }} src={logo} />
                        </Link>
                    </div>
                    <Menu
                        theme="dark"
                        defaultValue={[items.label]}
                        mode="inline"
                        items={localStorage.getItem("roles") == "ADMIN" ? items : items}
                        onClick={({ key }) => {
                            navigate(key);
                        }}
                    ></Menu>
                </Sider>
                <Layout className="site-layout">
                    <Content
                        style={{
                            margin: "0 16px",
                        }}
                    >
                        <div
                            className="site-layout-background"
                            style={{
                                padding: 24,
                                minHeight: 360,
                            }}
                        >
                            {children}
                        </div>
                    </Content>
                </Layout>
            </Layout>
        </StoreProvider>
    );
};

export default DefaultLayout;
